/* Recipe Summary */
div.wp-block-udemy-plus-recipe-summary {
    position: relative;
    margin-bottom: 1rem;
    width: 100%;
    border-width: 1px;
    border-style: solid;
    --tw-border-opacity: 1;
    border-color: rgb(209 213 219 / var(--tw-border-opacity));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .wp-block-udemy-plus-recipe-summary .bi {
    position: absolute;
    left: 0px;
    right: 0px;
    top: -1.75rem;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    margin-bottom: 0px;
    display: block;
    width: 3rem;
    --tw-bg-opacity: 1;
    background-color: rgb(255 255 255 / var(--tw-bg-opacity));
    padding: 0.5rem;
    text-align: center;
    font-size: 1.5rem;
    line-height: 2rem;
    --tw-text-opacity: 1;
    color: rgb(156 163 175 / var(--tw-text-opacity));
  }
  .wp-block-udemy-plus-recipe-summary .recipe-columns-2 {
    position: relative;
    -moz-columns: 2;
         columns: 2;
  }
  .wp-block-udemy-plus-recipe-summary .recipe-metadata {
    position: relative;
    text-align: center;
  }
  .wp-block-udemy-plus-recipe-summary .recipe-title {
    margin-bottom: 0.5rem;
    text-transform: uppercase;
    --tw-text-opacity: 1;
    color: rgb(107 114 128 / var(--tw-text-opacity));
  }
  .wp-block-udemy-plus-recipe-summary .recipe-columns-2-alt {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 1rem;
    border-top-width: 1px;
    border-bottom-width: 0px;
    border-left-width: 0px;
    border-right-width: 0px;
    border-style: solid;
    --tw-border-opacity: 1;
    border-top-color: rgb(209 213 219 / var(--tw-border-opacity));
  }
  .wp-block-udemy-plus-recipe-summary .recipe-columns-2-alt .recipe-columns-2:first-child {
    grid-column: span 2 / span 2;
  }
  .wp-block-udemy-plus-recipe-summary .recipe-columns-2-alt .recipe-metadata {
    padding-top: 2rem;
  }
  
  .wp-block-udemy-plus-recipe-summary .components-spinner {
    margin: 0 auto;
  }